@forward '../base/variables';
@use '../base/variables' as v;

.div-configuracion{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    margin-top: 2rem;
    margin-bottom: 2rem;

    p{
        text-align: center;
        font-family: v.$fuente_principal;
        font-weight: bold;
    }

    input{
        text-align: center;
        font-size: 2rem;
        width: fit-content;
    }
}

.boton-guardar{
    display: flex;
    width: fit-content;
    background-color: #03DFF8;
    padding: 1.5rem 4rem;
    border: none;
    font-size: 2rem;
    font-weight: bold;
    color: black;
    margin-top: 6rem;

    &:hover{
        cursor: pointer;
        background-color: #0CA6DF;
    }
}

